@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
.site-nav__list, .user-actions__link, .user-actions, .header__actions, .header__location-item {
  display: flex;
  align-items: center;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #E11C4B;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #E11C4B;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1180px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.header__location-wrapper {
  border-bottom: 2px solid #E8E8E8;
}
.header__location {
  display: flex;
  align-items: center;
  height: 35px;
}
.header__location-icon {
  width: 18px;
  height: 18px;
  margin-right: 6px;
}
.header__city {
  margin-right: 35px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
}
.header__pin {
  flex-shrink: 0;
  fill: none;
  stroke: #8D8D8D;
}
.header__shop-icon {
  fill: #8D8D8D;
}
.header__phone {
  margin-left: auto;
  padding-left: 24px;
  position: relative;
}
.header__phone:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background: url(../images/phone.svg) center no-repeat;
  position: absolute;
  top: 1px;
  left: 0;
}
.header__actions {
  padding: 8px 0;
}
.header__logo {
  margin-right: 12px;
}

.logo {
  width: 227px;
  height: 70px;
}

.search-form {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  border: 2px solid #DDDDDD;
  border-radius: 100px;
}
.search-form__btn {
  width: 26px;
  height: 26px;
  padding: 0;
  margin-right: 5px;
  background: none;
  border: none;
  cursor: pointer;
  fill: #E11C4B;
}
.search-form__btn-icon {
  width: 100%;
  height: 100%;
}
.search-form__input {
  width: 455px;
  border: none;
  outline: none;
}
.search-form__input::placeholder {
  color: #8D8D8D;
}

.user-actions {
  margin-left: auto;
  padding-top: 5px;
}
.user-actions__item:not(:last-child) {
  margin-right: 25px;
}
.user-actions__link {
  flex-direction: column;
}
.user-actions__icon {
  width: 18px;
  height: 18px;
  margin-bottom: 2px;
  fill: #8D8D8D;
}

.site-nav {
  position: relative;
}
.site-nav__item {
  margin-right: 36px;
}
.site-nav__item.swiper-slide {
  width: auto !important;
}
.site-nav__link--active {
  color: #E11C4B;
}
.site-nav__link--promotion {
  padding-left: 24px;
  position: relative;
}
.site-nav__link--promotion:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background: url(../images/promotion.svg) center no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}
.site-nav__btn.swiper-button-prev, .site-nav__btn.swiper-button-next {
  display: block;
  width: 18px;
  height: 18px;
  margin: 0;
  padding: 0;
  background: #fff;
  border: none;
  cursor: pointer;
  fill: none;
  stroke: #8D8D8D;
  position: absolute;
  top: 50%;
  z-index: 2;
  transform: translateY(-50%);
}
.site-nav__btn--prev.swiper-button-prev {
  left: 0;
}
.site-nav__btn--next.swiper-button-next {
  right: 0;
}
.site-nav__btn.swiper-button-next.swiper-button-disabled, .site-nav__btn.swiper-button-prev.swiper-button-disabled {
  opacity: 0;
}
.site-nav__btn-icon {
  width: 100%;
  height: 100%;
}